import React from "react"
import styled from "styled-components"
import { Link } from "gatsby"
import { HyperLink } from "../components/textstyling"

const FirstContent = styled.div`
  width: 100%;
  grid-area: header;
  display: grid;
  grid-template-columns: 1fr 2fr 2fr 2fr 1fr;
  grid-template-areas: "... logo ... sociallink ...";

  @media only screen and (max-width: 1025px) {
    grid-template-columns: 40px 2fr 2fr 2fr 40px;
  }

  @media only screen and (max-width: 450px) {
    grid-template-columns: 30px 1fr 3fr 30px;
    grid-template-areas: "... logo sociallink ...";
  }
`
const LogoHeader = styled.img`
  height: 50px;
`

const HomepageLink = styled(Link)`
  grid-area: logo;
  margin-top: 60px;
  height: 50px;
  width: 80px;
  @media only screen and (max-width: 450px) {
    margin-top: 30px;
  }
`

const SocialLink = styled.div`
  grid-area: sociallink;
`

const FollowUs = styled.div`
  float: right;
  text-align: right;
  margin-top: 59px;
  cursor: pointer;

  @media only screen and (max-width: 450px) {
    margin-top: 28px;
  }
`

const IgLogoHeader = styled.img`
  height: 32px;
`

export const Header = () => (
  <FirstContent>
    <HomepageLink to="/">
      <LogoHeader src="../32icons_logo.svg" alt="32Icons logo" />
    </HomepageLink>
    <SocialLink>
      <FollowUs>
        <IgLogoHeader src="../ig_logo.svg" alt="32Icons logo" />
      </FollowUs>
    </SocialLink>
  </FirstContent>
)
